import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';

@Component({
  selector: 'app-clientinstructions',
  templateUrl: './clientinstructions.page.html',
  styleUrls: ['./clientinstructions.page.scss'],
})
export class ClientinstructionsPage implements OnInit {
  bookingdata: any = {};
  extraServices = [];

  constructor(
    private modalCtrl: ModalController,
    public navParams: NavParams
  ) {
    this.bookingdata = this.navParams.get('data');
    if (this.bookingdata.extra_services) {
      this.extraServices = this.bookingdata.extra_services;
    }
  }

  ngOnInit() {}
  dismiss() {
    this.modalCtrl.dismiss();
  }
}
